// 
const request = (options) => {
	// 
	if (options.interceptRequest) {
		options = options.interceptRequest(options);
	}
	// uni.showLoading({
	// 	title: ""
	// })
	return new Promise((resolve, reject) => {
		uni.request({
			...options,
			success: (response) => {
				setTimeout(()=>{
					uni.hideLoading()
				},3000)
				// 
				if (options.interceptResponse) {
					const newResponse = options.interceptResponse(response);
					if (newResponse) {
						resolve(newResponse);
						return;
					}
				}
				resolve(response);
			},
			fail: (error) => {
				setTimeout(()=>{
					uni.hideLoading()
				},3000)
				reject(error);
			},
		});
	});
};

// 
const interceptRequest = (options) => {
	const token = uni.getStorageSync('token')
	// 
	options.header = {
		...options.header,
		Authorization: token, //  token
	};
	return options;
};

const interceptResponse = (response) => {
	// console.log('', response);
	// 
	const {
		code,
		messgage
	} = response.data
	if (response.statusCode !== 200) {
		switch (Number(code)) {
			case 999:
				uni.showToast({
					icon:'error',
					title: messgage,
					duration: 2000
				})
				return response;
			case 3:
				uni.showToast({
					icon:'error',
					title: messgage,
					duration: 2000
				})
				return null;
			case 2:
				uni.showToast({
					icon:'error',
					title: messgage,
					duration: 2000
				})
				return null;
			case 1:
				uni.redirectTo({
					url: '/pages/sonView/login/index'
				});
				return null;
			case 0:
				uni.showToast({
					icon:'error',
					title: messgage,
					duration: 2000
				})
				return null;
			default:
				return response;
		}
	}else{
		// 
		return response; //  resolve
	}

	// if (Number(response.statusCode) === 999) {
	// 	// 
	// 	uni.redirectTo({
	// 		url: '/pages/sonView/login/index'
	// 	});
	// 	return null; //  resolve
	// }else if ()
	return response;
};

//  GET 
const get = (url, data = {}, options = {}) => {
	// console.log('get')
	return request({
		url,
		data,
		method: 'GET',
		interceptRequest,
		interceptResponse,
		...options,
	});
};

//  POST 
const post = (url, data = {}, options = {}) => {
	// console.log('post')
	return request({
		url,
		data,
		method: 'POST',
		interceptRequest,
		interceptResponse,
		...options,
	});
};

export {
	get,
	post
};