import Vue from 'vue'
import Vuex from 'vuex'
import {getSetting} from '@/request/api.js'
Vue.use(Vuex)


const store = new Vuex.Store({
    state: {
		//mutations
		login: true, //
		token: '',//token
		config:{},//
		userInfo:{
			mini_openid:''
		}
	},
    mutations: {
		//
		loginStatus(state){
			state.login = true //
		},
		setToken(state,token){
			state.token = token// token
		},
		setAppid(state,data){
			state.userInfo.mini_openid = data  //appid
		},
		// 
		configInfo(state){
			getSetting().then((res)=>{
				console.log('',res.data.data);
			    state.config.open_image = res.data.data.open_image//URL  
			    state.config.mobile = res.data.data.mobile//  
			    state.config.donate_times = res.data.data.donate_times//  
			    state.config.donate_money = res.data.data.donate_money//
			    state.config.service_agreement = res.data.data.service_agreement//   
			    state.config.areas = res.data.data.areas//->
			    state.config.about_us = res.data.data.about_us//  
			    state.config.about_us_images = res.data.data.about_us_images// 
			    state.config.integral = res.data.data.integral//  - 
			})
		},
		// state
		setState(state){
			uni.setStorageSync('state', JSON.stringify(state))
		},
		// state
		getState(state){
			const stateObj = uni.getStorageSync('state')
			// console.log('state',stateObj);
			if(stateObj){
				const objs = JSON.parse(stateObj)
				state.login = objs.login
				state.token = objs.token
				state.userInfo.mini_openid = objs.userInfo.mini_openid
			}
		},
	},
    actions: {
		//,statemutations
		
	}
})
export default store
